.contact-section {
  & > a {
    margin-bottom: 5em;
  }
}

.contact-text {
  text-align: justify;
  color: $gray-D;
  width: 600px;
  line-height: 2em;
}

.mail {
  margin: 1em 0;
  color: $gray-7;
}

.all-social-links,
.social-links {
  display: flex;
  align-items: center;
  justify-content: space-between;

  a {
    opacity: 0.5;

    &:hover {
      opacity: 1;
    }
  }

  img {
    width: 80%;
  }
}

.all-social-links {
  a {
    width: 60px;
  }
}

.social-links a {
  width: 40px;
}

@media (max-width: 800px) {
  .contact-text {
    width: 100%;
  }
}

@media (max-width: 350px) {
  .all-social-links a {
    width: 50px;
  }
}
